.box {
  background-color: #561d89;
  cursor: pointer;
  width: 30%;
  padding: 20px;
  text-align: left;
  min-height: 76px;
  margin: 0 10px;
}

.box-title {
  font-family: 'Barlow Condensed', sans-serif;
  color: white;
  font-weight: 600;
  font-size: 20px;
  line-height: 18px;
}

.box-hr {
  color: #f7cd05;
  width: 30px;
  margin: 10px 0;
}